"use client";
import React, { use } from "react";
import dynamic from "next/dynamic";
import styles from "../Style/Brands.module.scss";

// Dynamically import react-slick for Next.js
const Slider = dynamic(() => import("react-slick"), { ssr: false });

// Example partner images
const partners = [
  { src: "/img/192x104_sponsor1.jpg", alt: "Colgeta", link: "#" },
  { src: "/img/192x104_sponsor2.jpg", alt: "Apamil", link: "#" },
  { src: "/img/192x104_sponsor3.jpg", alt: "Cetafill", link: "#" },
  { src: "/img/192x104_sponsor4.jpg", alt: "Nativu", link: "#" },
  { src: "/img/192x104_sponsor5.jpg", alt: "Aktins", link: "#" },
  { src: "/img/192x104_sponsor1.jpg", alt: "Colgeta", link: "#" },
  { src: "/img/192x104_sponsor1.jpg", alt: "Colgeta", link: "#" },
  { src: "/img/192x104_sponsor2.jpg", alt: "Apamil", link: "#" },
  { src: "/img/192x104_sponsor3.jpg", alt: "Cetafill", link: "#" },
  { src: "/img/192x104_sponsor4.jpg", alt: "Nativu", link: "#" },
  { src: "/img/192x104_sponsor5.jpg", alt: "Aktins", link: "#" },
  { src: "/img/192x104_sponsor1.jpg", alt: "Colgeta", link: "#" },
];

const PartnersSection = () => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    arrows: true,
    responsive: [
      { breakpoint: 1200, settings: { slidesToShow: 5 } },
      { breakpoint: 992, settings: { slidesToShow: 4 } },
      { breakpoint: 768, settings: { slidesToShow: 3 } },
      { breakpoint: 576, settings: { slidesToShow: 2 } },
    ],
  };

  return (
    <div className={styles.Brands}>
      <h3 className={styles.title}>Our Partners</h3>
      <Slider {...settings} className={styles.slider}>
        {partners.map((partner, index) => (
          <div key={index} className={styles.partnerItem}>
            <a href={partner.link} target="_blank" rel="noopener noreferrer">
              <img src={partner.src} alt={partner.alt} />
            </a>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default PartnersSection;
